@import url(https://fonts.googleapis.com/css2?family=Lora&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alegreya);
@import url(https://fonts.googleapis.com/css2?family=Bilbo+Swash+Caps);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Verdana, Consolas, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  scroll-behavior:smooth
}
.intro {
  transform: rotate(-30deg);
  font-family: 'Bilbo Swash Caps';
  padding-left: 1em;
}

.large-text {
  font-size: 5em;
}

.med-text {
  font-size: 3em;
}

.contact {
  font-family: 'Consolas';
}
.rotated {
  transform: rotate(-30deg);
}
